* {
  box-sizing: border-box;
}
.swiper-button-prev,
.swiper-button-next {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 0;
  width: 80px;
  height: 80px;
  font-size: 52px;
  background-color: rgba(66,66,66,0.7);
  color: #fff;
  transition: 0.2s background-color ease;
}
.swiper-button-prev._light,
.swiper-button-next._light {
  background-color: rgba(214,214,214,0.6);
  color: #222;
}
.swiper-button-prev._light:hover,
.swiper-button-next._light:hover {
  background-color: #d6d6d6;
}
.swiper-button-prev:hover,
.swiper-button-next:hover {
  background-color: #424242;
}
.swiper-button-prev::after,
.swiper-button-next::after {
  content: '';
}
.swiper-button-prev {
  left: 0;
}
.swiper-button-next {
  right: 0;
}
.swiper-container-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet:not(:last-child) {
  margin: 0 32px 0 0;
}
.swiper-pagination {
  bottom: 40px;
}
.swiper-pagination .swiper-pagination-bullet {
  width: 12px;
  height: 12px;
  background: transparent;
  border: 1px solid #fff;
  opacity: 1;
}
.swiper-pagination .swiper-pagination-bullet-active {
  background: #fff;
}
.first-screen-slider {
  padding-bottom: 80px;
}
.props {
  display: flex;
  justify-content: center;
  align-items: center;
}
.props._lg .props__item {
  min-height: 80px;
}
.props._light {
  color: #fff;
}
.props._light .props__desc {
  color: #fff;
}
.props._left {
  justify-content: flex-start;
}
.props__item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 12px;
  min-width: 120px;
  min-height: 60px;
}
.props__item._title {
  padding-right: 40px;
}
.props__item:not(:last-child) {
  border-right-width: 1px;
  border-right-style: solid;
  border-right-color: #d6d6d6;
}
.props__desc {
  color: #808080;
}
